import React from 'react';
import Types from 'prop-types';
import WithTextFormat from '../withTextFormat';

const InputWithTextFormat = props => (
  <WithTextFormat {...props} render={renderProps => <input {...renderProps} />} />
);

InputWithTextFormat.propTypes = {
  textFormatPattern: Types.string.isRequired,
  onChange: Types.func.isRequired,
};

export default InputWithTextFormat;



