import React from 'react';
import { Select, Checkbox } from '../../../src';

const generateInput = (knobs, componentThis) => {
  const { type, label, state, options } = knobs;

  const handleOnChangeSelect = selection =>
    componentThis.setState({ [state]: selection ? { ...selection } : undefined });

  const handleOnChange = event => componentThis.setState({ [state]: event.target.value });

  const handleOnChangeCheckbox = value => componentThis.setState({ [state]: value });

  const handleOnChangeNumber = event =>
    componentThis.setState({ [state]: parseFloat(event.target.value) });

  switch (type) {
    case 'select':
      return (
        <div className="col-md-6 m-b-2" key={state}>
          <label htmlFor={state} className="control-label">
            {label}
          </label>
          <Select
            id={state}
            selected={componentThis.state[state] ? { ...componentThis.state[state] } : null}
            options={options.map(option => ({ ...option }))}
            required
            onChange={handleOnChangeSelect}
          />
        </div>
      );

    case 'checkbox':
      return (
        <div className="col-md-6 m-b-2" key={state}>
          <label htmlFor={state} className="control-label">
            {label}
          </label>
          <Checkbox
            id={state}
            label={label}
            onChange={handleOnChangeCheckbox}
            checked={componentThis.state[state]}
          />
        </div>
      );

    case 'number':
      return (
        <div className="col-md-6 m-b-2" key={state}>
          <label htmlFor={state} className="control-label">
            {label}
          </label>
          <input
            id={state}
            type="number"
            className="form-control"
            value={componentThis.state[state]}
            onChange={handleOnChangeNumber}
          />
        </div>
      );

    case 'input':
    default:
      return (
        <div className="col-md-6 m-b-2" key={state}>
          <label htmlFor={state} className="control-label">
            {label}
          </label>
          <input
            id={state}
            type="text"
            className="form-control"
            value={componentThis.state[state]}
            onChange={handleOnChange}
          />
        </div>
      );
  }
};

export default generateInput;



